@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');
 html {
	 height: 100%;
	 margin: 0;
	 padding: 0;
}
 body {
	 font: 100% Roboto, sans-serif;
	 margin: 0;
	 color: #333;
	 font-size: 1rem;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 height: 100%;
	 background-color: #511730;
}
 #quote-box {
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: column;

	 text-align: center;
	 padding: 3rem;
	 border: 5px solid #cb9173;
	 height: 62.5vh;
	 width: 62.5vw;
}
 #text {
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: column;
	 color: white;
	 font-size: 1.7rem;
	 width: 50vw;
	 padding-bottom: 14.7vh;
}
 #quotes {
	 width: 100px;
	 height: 200px;
}
 #author {
	 color: white;
}
 #new-quote {
	 margin-top: 1rem;
}
 #tweet-quote {
	 margin-top: 1rem;
}
 